import { Col, Card, Row, Container, foods, Button, ListGroup, Alert, Table} from "react-bootstrap";
import {Navigate, useNavigate} from 'react-router-dom';
import {useState, useEffect} from 'react';
import Carousel from 'react-bootstrap/Carousel';


function Store() {

    const [stores, setStores] = useState([
        {
            title: " ",
            text: "   ",
            life: 3,
            src: "/images/market1.jpg"
        },
        {
            title: " ",
            text: "    ",
            life: 2,
            src: "/images/market2.jpg"
        },
        {
            title: "  ",
            text: "     ",
            life: 5,
            src: "/images/market3.jpg"
        },
        {
            title: "  ",
            text: "  , ,   ",
            life: 1,
            src: "/images/market4.jpg"
        },
        {
            title: " ",
            text: "     .",
            life: 5,
            src: "/images/market5.jpg"
        },
        {
            title: "  ",
            text: "     .",
            life: 3,
            src: "/images/market6.jpg"
        },
        {
            title: "  ",
            text: "  ,   ",
            life: 2,
            src: "/images/market7.jpg"
        },
        {
            title: "  ",
            text: "     .",
            life: 2,
            src: "/images/noti7.jpg"
        },
        {
            title: "  ",
            text: "        .",
            life: 1,
            src: "/images/market9.jpg"
        }
    ]);



    let [carts, setCarts] = useState([ ]);
    
    let [add, setAdd] = useState(0);

    let navigate = useNavigate();


    return (
      <div className="topStore">
        <img
          className="d-block w-100"
          alt="Third slide"
          src={process.env.PUBLIC_URL + "/images/store8.jpg"}
          width="80%"
          height="500px"
        />

        <div style={{ height: "100px" }}></div>
        <h1 style={{ textAlign: "center" }}>Store</h1>
        <div style={{ height: "20px" }}></div>

        {stores.map(function (data, i) {
          return (
            <div className="store">
              <Card style={{ width: "18rem" }}>
                <Card.Img
                  variant="top"
                  src={process.env.PUBLIC_URL + data.src}
                  width="100%"
                  height="250px"
                  margin="20px"
                />
                <Card.Body>
                  <Card.Title><p className="storeText">{data.title}</p></Card.Title>
                  <Card.Text>
                    {data.text}
                    <div style={{ height: "10px" }}></div>
                    <p style={{ fontColor: "red" }} className="storeLifeText">
                      : + {data.life} 
                    </p>
                  </Card.Text>
                  <Button
                    variant="light"
                    className="storeButton"
                    onClick={() => {
                      // let temp = [...carts]
                      // temp.push(data);
                      // setCarts(temp);
                      setCarts([...carts, data]);
                      setAdd(add + data.life);
                    }}
                  >
                    
                  </Button>
                </Card.Body>
              </Card>
            </div>
          );
        })}

        <div style={{ height: "100px" }}></div>
        <h1 style={{ textAlign: "center" }}>Cart</h1>
        <div style={{ height: "50px" }}></div>

        <Table bordered className="cartTable">
          <thead>
            <tr className="tableMainText">
              <th></th>
              <th width="600px"></th>
              <th></th>
            </tr>
          </thead>
          <tbody className="tableMainInText">
            {carts.map(function (data, i) {
              return (
                <>
                  <tr>
                    <td rowSpan={2} width="250px" height="220px">
                      <img
                        src={process.env.PUBLIC_URL + data.src}
                        width="200px"
                        height="200px"
                        margin="auto"
                      ></img>
                    </td>
                    <td >
                    {data.title}
                      <Button
                        variant="light"
                        onClick={() => {
                          // .     carts    
                          let temp = [...carts];
                          temp.splice(i, 1);
                          setCarts(temp);
                          setAdd(add - data.life);
                        }}
                      >
                        X
                      </Button>
                    </td>
                    <td rowSpan={2}>{data.life} </td>
                  </tr>
                  <tr>
                    <td>{data.text}</td>
                  </tr>
                </>
              );
            })}
            <tr>
              <td colSpan={2}></td>
              <td>{add} </td>
            </tr>
          </tbody>
        </Table>

        <div style={{ height: "100px" }}></div>
        <h1 style={{ textAlign: "center" }}>Action</h1>
        <div style={{ height: "50px" }}></div>
        <Card className="actionCard">
            <Card.Body><p className="actionText">    = <span
            style={{color:"red"}}>{add} </span>
          <Button variant="light" onClick={()=>
            {alert(`.      ${add} .`)
            navigate("/Todo")}
            }
            > </Button></p></Card.Body>
        </Card>
      
      </div>
    );
    }


export default Store



